import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
 
import { ApprovalService } from '../../angular-services/approval.service';
import { Approval } from '../../classes/approval';

@Component({
  selector: 'app-edit-approval-form-admin',
  templateUrl: './edit-approval-form.component.html',
  styleUrls: ['./edit-approval-form.component.css']
})
export class EditApprovalFormComponent implements OnInit {

  @Output() approvalName = new EventEmitter<string>();
  approval: Approval = new Approval();
  formEditApproval: FormGroup;
  alert: any = { type: '', message: '', show: false };

  constructor(private approvalService: ApprovalService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getApproval();
  }

  initForm() {
    this.formEditApproval = new FormGroup({
      name: new FormControl('', Validators.required),
      amount: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getApproval() {
    let key = this.route.snapshot.paramMap.get('id');
    this.approvalService.getApproval(key).subscribe(
      response => {
        if (response !== undefined) {
          this.approval = response;
          this.approvalName.emit(this.approval.name);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditApproval.patchValue({
      name: this.approval.name,
      amount: this.approval.amount,
      status: this.approval.status+''
    });
  }

  save() {
    if (this.formEditApproval.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditApproval.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.approvalService.updateApproval(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }
}
